//iconfont setting
@import "_mixin";
@import "_variables";

%iconfont {
	font-family: 'icomoon' !important;
	speak: none;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;
	vertical-align: top;

	/* Better Font Rendering =========== */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}s

%indent{
  overflow: hidden;
  text-indent: 100%;
  white-space: nowrap;
}
%flexLeft{
  display: flex;
  justify-content:flex-start;
  align-items: flex-start;
}
%flexSpace{
  display: flex;
  justify-content:space-between;
  align-items: center;
}
%flexCenter{
  display: flex;
  justify-content:center;
  align-items: center;
}
%flexColumn{
  display: flex;
  flex-direction: column;
  align-items: center;
}
%arrowRight{
  content: "";
  width: 0;
	height: 0;
	border-top: 8px solid transparent;
	border-left: 16px solid $white;
	border-bottom: 8px solid transparent;
  position: absolute;
  top: 50%;
  margin-top: -6px;
}
%arrowBottom{
 content: "";
  width: 0;
	height: 0;
	border-left: 8px solid transparent;
	border-right: 8px solid transparent;
	border-top: 16px solid $white;
  position: absolute;
  top: 50%;
  margin-top: -6px; 
}
%list{
  display: block;
  letter-spacing: -.4em;
  text-align: center;
}
%txt{
  @include fz(18);
  line-height: 1.7;
  font-weight: bold;
  @include max-screen($TB01blake){
    @include fz(14);
  }
}
%inner{
  max-width: 1150px;
  margin: 0 auto;
  @include max-screen($TB01blake){
    width: 90%;
    margin: 0 auto;
  }
}
%triDown{
  content: "";
  position: absolute;
  bottom: -25px;
  left: 0;
  right: 0;
  margin: auto;
  width: 0;
  height: 0;
  border-left: 25px solid transparent;
  border-right: 25px solid transparent;
  border-top: 25px solid red;
  @include max-screen($TB01blake){
  }
}

