@charset "utf-8";
@import "_format";
@import "_variables";
@import "_extend";
@import "_mixin";
/* ********************************************************
	add style CSS
******************************************************** */

/*-------------------------------
  Font Set - Color
-------------------------------*/

/*-------------------------------
  common
-------------------------------*/
html{
	overflow: auto;
}
html,body{
	height: 100%;
	font-size: 62.5%;
  font-feature-settings : "palt";
  text-align: justify;
  text-justify: inter-ideograph;
  font-weight: bold;
  line-height: 1.6;
  color: $bk;
}

body {
  -webkit-text-size-adjust: 100%;
	font-family: $font_yugo;
  font-feature-settings : "palt";
  font-weight: normal;
}
a[href^="tel:"]{
	text-decoration: none;
}
a[href^="tel:"]:hover{
	cursor: default;
}

a{
	text-decoration: none;
}
.pc{
  @include max-screen($SP01blake) {
    display: none !important;
  }
}

.sp{
  @include min-screen($SP01blake){
    display: none !important;
  }
}
#container{
  width: 100%;
  box-sizing: border-box;
  @include max-screen($SP01blake){
		padding-top: 60px;
	}
} 

.fadein {
   opacity : 0;
  transform: translateY(30px);
  transition: all 1s;
}


/*-------------------------------
  Header
-------------------------------*/
#pageHeader{
	background: $pink;
	width: 100%;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 999;
	@include max-screen($SP01blake){
	  background: $pink;
	}
	#headerInner{
		max-width: 980px;
		margin: 0 auto;
		position: relative;
		@include max-screen($SP01blake){
			height: 60px;
		}
		.logo{
			background: url(../images/osaka/img_logo02.svg) center center no-repeat;
			background-size: contain;
			width: 400px;
			height: 70px;
			text-indent: 100%;
			overflow: hidden;
			white-space: nowrap;
			position: absolute;
			top: 0;
			left: 0;
			display: none;
			@include max-screen($SP01blake){
				display: block;
				background-size: contain;
				width: 40%;
				height: 60px;
				position: fixed;
				top: 0;
				left: 10px;
			}
			@include max-screen($SP02blake){
				width: 30%;
			}
		}
		.nav{
			position: absolute;
			top: 30px;
			right: 0;
			@include flex-a;
			@include max-screen($SP01blake){
				top: 0;
			}
			li{
				@include max-screen($SP01blake){
					width: 60px;
				}
				a{
					color: $white;
					@include fz(14);
					font-weight: bold;
					transition: $base-transition;
					line-height: 1;
					@include max-screen($SP01blake){
						background: $pink;
						height: 60px;
						position: relative;
						@include fz(10);
						padding-top: 45px;
						box-sizing: border-box;
						display: block;
						text-align: center;
						@include max-screen($SP02blake){
							@include fz(8);
						}
					}
					&:before{
						@extend %iconfont;
						@include fz(14);
						color: $white;
						line-height: 1;
						display: inline-block;
						margin-right: 10px;
						@include max-screen($SP01blake){
							display: none;
						}
					}
					&:after{
						@include max-screen($SP01blake){
							content: "";
							background-position: center center;
							background-repeat: no-repeat;
							background-size: contain;
							width: 30px;
							height: 30px;
							position: absolute;
							top: 10px;
							left: 0;
							right: 0;
							margin: auto;
							display: block;
						}
					}
					&:hover{
						color: $or01;
						&:before{
							color: $or01;
						}
					}
				}
				&:nth-child(1){
					a{
						&:before{
							content: "\e9b8";
						}
						&:after{
							@include max-screen($SP01blake){
								background-image:url(../images/osaka/nav01.svg);
							}
						}
					}
				}
				&:nth-child(2){
					a{
						&:before{
							content: "\e93e";
						}
						&:after{
							@include max-screen($SP01blake){
								background-image: url(../images/osaka/nav02.svg);
							}
						}
					}
				}
				&:nth-child(3){
					a{
						&:before{
							content: "\e947";
						}
						&:after{
							@include max-screen($SP01blake){
								background-image: url(../images/osaka/nav03.svg);
							}
						}
					}
				}
				&+li{
					margin-left: 20px;
					@include max-screen($SP01blake){
						margin-left: 0;
						border-left: 1px solid $white;
					}
				}
			}
		}
	}
	&.fixed{
		height: 70px;
		box-shadow: 3px 0 5px rgba(0,0,0,.2);
		@include max-screen($SP01blake){
			height: 60px;
		}
		#headerInner{
			.logo{
				display: block;
			}
			.nav{
				li{
					a{
						&:before{
							color: $white;
							@include max-screen($SP01blake){
								color: $white;
							}
						}
						&:hover{
							color: $or01;
							&:before{
								color: $or01;
							}
						}
					}
				}
			}
		}
	}
}
/*-------------------------------
  共通パーツ
-------------------------------*/
.subTtl{
	@include fz(30);
	font-weight: bold;
	margin: 0 auto;
	text-align: center;
	color: $pink;
	line-height: 1;
	span{
		@include fz(14);
		color: $bk;
		display: block;
		margin-top: 5px;
		font-weight: normal;
		@include max-screen($SP01blake){
			@include fz(14);
		}
	}
}
.btn{
	background: $pink;
	width: 400px;
	height: 80px;
	margin: 0 auto;
	display: block;
	color: $white;
	@include fz(28);
	text-align: center;
	line-height: 80px;
	border-radius: 50px;
	transition: $base-transition;
	box-shadow: 10px 10px 0 0 #8e7a6d;
	@include max-screen($SP01blake){
	  width: 90%;
		height: 60px;
		line-height: 60px;
		@include fz(20);
		box-shadow: 5px 5px 0 0 #8e7a6d;
	}
	&:hover{
		background: $or02;
		box-shadow: none;
    transform: translate3d(0,10px,0);
	}
}
/*-------------------------------
  contents
-------------------------------*/
.mv{
	@include flex-a;
  background: url(../images/osaka/img_mv.png) center bottom no-repeat;
	background-size: cover;
	width: 100%;
	height: 700px;
	@include max-screen($SP01blake){
		height: calc(90vh - 60px);
	}
	h1{
		flex-wrap: wrap;
		text-align: center;
		color: $white;
		span{
			display: block;
			&.ptn01{
				@include fz(14);
				@include max-screen($SP01blake){
				}
			}
			&.logo{
				max-width: 210px;
				margin: 20px auto;
				@include max-screen($SP01blake){
					max-width: 100%;
					width: 60%;
					margin: 20px auto;
				}
			}
		}
	}
}
.catch{
	width: 100%;
	padding: 80px 0;
	@include max-screen($SP01blake){
		padding: 40px 15px;
		box-sizing: border-box;
	}
	h2{
		@include fz(30);
		color: $pink;
		text-align: center;
		@include max-screen($SP01blake){
			@include fz(20);
		}
		@include max-screen($SP02blake){
			@include fz(18);
		}
		span{
			display: inline-block;
			&:after{
				content: "";
				background: $or01;
				display: block;
				width: 100%;
				height: 10px;
				margin-top: -15px;
				@include max-screen($SP01blake){
					margin-top: -10px;
				}
			}
		}
		&+p{
			margin-top: 40px;
			display: block;
			@include fz(18);
			color: $bk;
			text-align: center;
			line-height: 2;
			@include max-screen($SP01blake){
				@include fz(14);
			}
		}
	}
}

.scheduleArea{
	background: #ffe5ee;
	width: 100%;
	padding: 80px 0;
		@include max-screen($SP01blake){
			padding: 40px 0;
			box-sizing: border-box;
		}
	&__ttl{
		&.subTtl{
		}
	}
	&__list{
		max-width: 650px;
		margin: 80px auto 0;
		li{
			@include flex;
			align-items: center;
			.scheduleArea__list__fig{
			}
			.scheduleArea__list__time{
				@include fz(18);
				margin-left: 40px;
			}
			&+li{
				margin-top: 40px;
			}
		}
	}
}
.systemArea{
	width: 100%;
	padding: 80px 0;
	@include max-screen($SP01blake){
		padding: 40px 0;
		box-sizing: border-box;
	}
	&__txt{
		margin-top: 80px;
		@include fz(18);
		text-align: center;
	}
}
.access{
	padding: 80px 0 0;
	&__inner{
		margin-top: 80px;
		@include flex-w-js;
			@include max-screen($SP01blake){
				margin-top: 40px;
				flex-direction: column;
			}
		&__map{
			width: 50%;
			height: 500px;
			@include max-screen($SP01blake){
				width: 80%;
				height: 250px;
				margin: 0 auto;
			}
		}
		&__add{
			@include flex-a;
			background: $pink;
			width: 50%;
			height: 500px;
			color: $white;
			text-align: center;
			@include fz(16);
			@include max-screen($SP01blake){
				width: 100%;
				height: auto;
				padding: 40px 20px;
				box-sizing: border-box;
				margin-top: 40px;
				@include fz(14);
			}
			h2{
				@include fz(18);
				@include max-screen($SP01blake){
					@include fz(14);
				}
				span{
					@include fz(22);
					display: inline-block;
					@include max-screen($SP01blake){
						@include fz(18);
					}
				}
				&+p{
					margin-top: 1em;
					&+p{
						margin-top: 1em;
					}
				}
			}
		}
	}
}



/*-------------------------------
  Footer
-------------------------------*/
#pageFooter{
	width: 100%;
	position: relative;
	#footerInner{
		max-width: 980px;
		margin: 0 auto;
		padding: 40px 0;
		text-align: center;
		color: $bk;
		@include fz(12);
		@include max-screen($SP01blake){
			padding: 20px 0;
		}
		#copyright{
			&+p{
				margin-top: 1em;
			}
		}
	.pageTop {
			position: absolute;
			right: 30px;
			bottom: 115px;
			display: block;
			z-index: 9999;
		@include max-screen($SP01blake){
			right: 20px;
			bottom: 80px;
		}
		a{
			overflow: hidden;
			text-indent: 100%;
			white-space: nowrap;
			display: block;
			width: 60px;
			height: 60px;
			background: #fff;
			border-radius: 50%;
			transition: all .3s ease;
			position: relative;
			box-shadow: 3px 0 5px rgba(0,0,0,.2);
				@include max-screen($SP01blake){
					width: 40px;
					height: 40px;
				}
				&:before{
					content: "";
					display: block;
					position: absolute;
					width: 20px;
					height: 20px;
					border-top: 2px solid #332f3d;
					border-right: 2px solid #332f3d;
					transform: rotate(-45deg);
					top: 15px;
					bottom: 0;
					right: 0;
					left: 0;
					margin: auto;
					@include max-screen($SP01blake){
						top: 5px;
						width: 10px;
						height: 10px;
					}
				}
			}
		}
	}
}
